@import url("https://fonts.googleapis.com/css2?family=Source+Sans+3:wght@400;600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=SF+Pro+Text:wght@600;700&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --leaf-label-large: "Source Sans 3";
  --font-roboto: Roboto;
  --ecotrace-m3-label-small: "Source Sans Pro";
  --body-bold: "SF Pro Text";

  /* font sizes */
  --font-size-lg: 18px;
  --leaf-label-large-size: 14px;
  --leaf-body-small-size: 12px;
  --ecotrace-m3-label-small-size: 11px;
  --font-size-3xs: 10px;
  --body-bold-size: 17px;
  --ecotrace-m3-title-large-size: 20px;
  --ecotrace-m3-title-medium-size: 16px;
  --ecotrace-m3-headline-small-size: 24px;

  /* Colors */
  --color-aliceblue-100: #e7ecf2;
  --leaf-sys-light-secondary-container: #e4edf3;
  --color-slategray: #506281;
  --leaf-sys-light-on-surface-variant: #6e7469;
  --leaf-sys-light-on-secondary-container: #1e2a3d;
  --color-royalblue-100: #1877f2;
  --color-royalblue-200: #0c57b8;
  --leaf-sys-light-error: #ba1a1a;
  --leaf-sys-light-outline-variant: #eaefe4;
  --ecotrace-m3-sys-light-on-surface: #f4f4f4;
  --leaf-sys-light-background: #f2f3f7;
  --color-whitesmoke-100: #ebeaec;
  --ecotrace-m3-sys-light-on-primary: #fff;
  --label-color-light-primary: #000;
  --ecotrace-m3-sys-light-on-surface-variant: #8a8a8a;
  --ecotrace-m3-ref-neutral-neutral50: #767872;
  --ecotrace-m3-sys-light-secondary-container: #d0e5cf;
  --ecotrace-m3-sys-light-primary: #4caf50;
  --color-goldenrod: #f9b447;
  --ecotrace-m3-ref-primary-primary20: #003a02;
  --ecotrace-m3-sys-light-surface: #fcfdf6;
  --ecotrace-m3-sys-light-secondary: #315da8;
  --ecotrace-m3-source-secondary: #315ca7;
  --ecotrace-m3-sys-light-on-secondary-container: #3357a5;
  --ecotrace-m3-sys-light-surface-tint: #168350;
  --ecotrace-m3-ref-primary-primary40: #006e07;

  /* Gaps */
  --gap-7xs: 6px;
  --gap-xs: 12px;
  --gap-3xs: 10px;
  --gap-5xs: 8px;
  --gap-5xl: 24px;
  --gap-base: 16px;
  --gap-9xs: 4px;

  /* Paddings */
  --padding-5xs: 8px;
  --padding-9xs: 4px;
  --padding-base: 16px;
  --padding-lg: 18px;
  --padding-xs: 12px;
  --padding-5xl: 24px;
  --padding-xl: 20px;
  --padding-13xl: 32px;

  /* border radiuses */
  --br-81xl: 100px;
  --br-5xs: 8px;
  --br-base: 16px;
  --br-9xs: 4px;
  --br-5xl: 24px;
  --br-13xl: 32px;
  --br-36xl: 55px;
  --br-8xl-5: 27.5px;
  --br-9xl: 28px;

  /* Effects */
  --dynamic-shadow-effect: 0px 8px 16px rgba(0, 0, 0, 0.25);
  --ecotrace-m3-elevation-light-3: 0px 4px 8px 3px rgba(0, 0, 0, 0.1),
    0px 1px 3px rgba(0, 0, 0, 0.05);
  --ecotrace-m3-elevation-dark-1: 0px 1px 3px 1px rgba(0, 0, 0, 0.15),
    0px 5px 25px rgba(0, 0, 0, 0.15);
}
